@import '../../global/global.css';

:host {
  display: block;
}

/* Base picker menu styles - matching dropdown menu */
.picker-menu {
  width: fit-content;
  min-width: 320px;
  background: var(--color-white);
  border-radius: var(--radius-md);
  box-shadow: 0px 32px 64px -12px rgba(64, 73, 104, 0.14),
    0px 0px 1px 1px rgba(64, 73, 104, 0.1);
  border: 1px solid var(--color-neutral-200);
  overflow: hidden;
  flex-shrink: 0;
  position: relative;
  box-sizing: border-box;
}

/* Animation classes */
.picker-menu.animate-dropdown-enter {
  animation: dropdownEnter 0.15s ease-out forwards;
}

.picker-menu.animate-dropdown-exit {
  animation: dropdownExit 0.15s ease-out forwards;
}

@keyframes dropdownEnter {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes dropdownExit {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-8px);
  }
}

/* No footer variant */
.picker-menu.no-footer {
  padding-bottom: var(--spacing-md);
}

/* Footer - following dropdown menu pattern */
.picker-menu-footer {
  border-top: 1px solid var(--color-neutral-200);
  padding: var(--spacing-xl) var(--spacing-xl);
  background: var(--color-white);
}

.picker-menu-actions {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: var(--spacing-lg);
}

/* When today button is present, use space-between layout */
.picker-menu-actions.has-today-button {
  justify-content: space-between;
}

.picker-menu-main-actions {
  display: flex;
  gap: var(--spacing-lg);
}
