import { Component, Prop, State, Event, EventEmitter, Watch, h, Host } from '@stencil/core';
import { MonthYear, DisabledMonth } from '../bh-month-picker-content/bh-month-picker-content';

@Component({
  tag: 'bh-picker-menu',
  styleUrl: 'bh-picker-menu.css',
  shadow: true,
})
export class BhPickerMenu {
  /**
   * Minimum year in the picker
   */
  @Prop() minYear: number = 1998;

  /**
   * Maximum year in the picker
   */
  @Prop() maxYear: number = 2060;

  /**
   * Whether the picker is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Array of disabled months
   */
  @Prop() disabledMonths: DisabledMonth[] = [];

  /**
   * Animation class for the menu
   */
  @Prop() animationClass: string = 'animate-dropdown-enter';

  /**
   * Whether the menu is visible
   */
  @Prop() visible: boolean = true;

  /**
   * Whether to show the footer with action buttons
   */
  @Prop() showFooter: boolean = true;

  /**
   * Whether to show the today button
   */
  @Prop() showTodayButton: boolean = false;

  /**
   * Whether the apply button is disabled
   */
  @Prop() disableApply: boolean = false;

  /**
   * Current value
   */
  @Prop() value?: MonthYear;

  @State() isClosing: boolean = false;
  @State() shouldRender: boolean = true;
  @State() selectedMonth: number | null = null;
  @State() selectedYear: number | null = null;

  /**
   * Event emitted when value changes
   */
  @Event() bhChange!: EventEmitter<MonthYear | null>;

  /**
   * Event emitted when a month/year is selected
   */
  @Event() bhMonthYearSelect!: EventEmitter<MonthYear>;

  /**
   * Event emitted when cancel is clicked
   */
  @Event() bhCancel!: EventEmitter<void>;

  /**
   * Event emitted when apply is clicked
   */
  @Event() bhApply!: EventEmitter<void>;

  /**
   * Event emitted when today button is clicked
   */
  @Event() bhToday!: EventEmitter<void>;

  @Watch('visible')
  handleVisibleChange(newValue: boolean) {
    if (newValue) {
      this.shouldRender = true;
      this.isClosing = false;
    } else {
      this.isClosing = true;
      setTimeout(() => {
        this.shouldRender = false;
        this.isClosing = false;
      }, 150);
    }
  }

  @Watch('value')
  handleValueChange(newValue: MonthYear | undefined) {
    if (newValue) {
      this.selectedMonth = newValue.month;
      this.selectedYear = newValue.year;
    }
  }

  componentWillLoad() {
    this.shouldRender = this.visible;

    if (this.value) {
      this.selectedMonth = this.value.month;
      this.selectedYear = this.value.year;
    } else {
      // Initialize with current date
      const now = new Date();
      const currentMonth = now.getMonth();
      const currentYear = now.getFullYear();

      if (currentYear >= this.minYear && currentYear <= this.maxYear) {
        this.selectedMonth = currentMonth;
        this.selectedYear = currentYear;
      } else if (currentYear < this.minYear) {
        this.selectedYear = this.minYear;
        this.selectedMonth = currentMonth;
      } else {
        this.selectedYear = this.maxYear;
        this.selectedMonth = currentMonth;
      }
    }
  }

  private handleMonthYearSelect = (event: CustomEvent<MonthYear>) => {
    const selection = event.detail;
    this.selectedMonth = selection.month;
    this.selectedYear = selection.year;
    this.bhChange.emit(selection);
    this.bhMonthYearSelect.emit(selection);
  };

  private handleCancel = () => {
    this.bhCancel.emit();
  };

  private handleApply = () => {
    this.bhApply.emit();
  };

  private handleToday = () => {
    this.bhToday.emit();
  };

  render() {
    if (!this.shouldRender) {
      return null;
    }

    const menuClasses = {
      'picker-menu': true,
      'animate-dropdown-exit': this.isClosing,
      [this.animationClass]: !this.isClosing,
      'no-footer': !this.showFooter,
    };

    return (
      <Host>
        <div class={menuClasses}>
          {/* Main Content Area - render slot if provided, otherwise MonthPickerContent */}
          <slot>
            <bh-month-picker-content
              minYear={this.minYear}
              maxYear={this.maxYear}
              disabled={this.disabled}
              disabledMonths={this.disabledMonths}
              scrollable={true}
              selectedMonth={this.selectedMonth ?? undefined}
              selectedYear={this.selectedYear ?? undefined}
              onBhChange={this.handleMonthYearSelect}
            ></bh-month-picker-content>
          </slot>

          {/* Footer Actions - conditionally rendered */}
          {this.showFooter && (
            <div class="picker-menu-footer">
              <div class={`picker-menu-actions ${this.showTodayButton ? 'has-today-button' : ''}`}>
                {/* Today button on the left */}
                {this.showTodayButton && (
                  <bh-button
                    hierarchy="secondary"
                    size="sm"
                    icon="only"
                    iconName="today"
                    onBhClick={this.handleToday}
                  ></bh-button>
                )}

                {/* Cancel and Apply buttons always on the right */}
                <div class="picker-menu-main-actions">
                  <bh-button
                    hierarchy="secondary"
                    size="sm"
                    label="Cancel"
                    onBhClick={this.handleCancel}
                  ></bh-button>
                  <bh-button
                    hierarchy="primary"
                    size="sm"
                    label="Apply"
                    disabled={this.disableApply}
                    onBhClick={this.handleApply}
                  ></bh-button>
                </div>
              </div>
            </div>
          )}
        </div>
      </Host>
    );
  }

}
